<template>
  <transition name="fade">
    <div
      class="hevue-imgpreview-wrap"
      id="hevue-imgpreview-wrap"
      v-if="show"
      ref="heImg"
      @mouseup="removeMove('pc')"
      @touchend="removeMove('mobile')"
      @click.stop="clickMask"
    >
      <div class="he-img-wrap">
        <div
          class="heimgfont hevue-img-status-icon rotate-animation"
          v-show="imgState === 1"
        >
          &#xe6b1;
        </div>
        <!-- <div class="heimgfont loading">&#xe6b1;</div> -->
        <img
          :src="imgurl"
          ref="heImView"
          @click.stop=""
          v-show="imgState === 2"
          class="he-img-view"
          :style="
            'transform: scale(' +
              imgScale +
              ') rotate(' +
              imgRotate +
              'deg);margin-top:' +
              imgTop +
              'px;margin-left:' +
              imgLeft +
              'px;' +
              maxWH
          "
          @mousedown="addMove"
          @touchstart="addMoveMobile"
        />
        <!--  -->
        <div
          class="heimgfont hevue-img-status-icon"
          v-show="imgState === 3"
        >
          &#xec0d;
        </div>
        <!--  -->
        <div
          class="heimgfont he-close-icon"
          @click.stop="close"
          v-if="closeBtn"
        >
          &#xe608;
        </div>
        <!--  -->
        <div
          class="arrow arrow-left heimgfont"
          @click.stop="toogleImg(false)"
          v-if="arrowBtn && multiple"
        >
          &#xe620;
        </div>
        <!--  -->
        <div
          class="arrow arrow-right heimgfont"
          @click.stop="toogleImg(true)"
          v-if="arrowBtn && multiple"
        >
          &#xe60d;
        </div>
        <!--  -->
        <div class="he-control-bar-wrap" v-if="controlBar">
          <div
            class="he-control-bar"
            @click.stop
          >
            <!--  -->
            <div class="he-control-btn heimgfont" @click.stop="scaleFunc(-0.15)">
              &#xe65e;
            </div>
            <!--  -->
            <div class="he-control-btn heimgfont" @click.stop="scaleFunc(0.15)">
              &#xe65d;
            </div>
            <!--  -->
            <div
              class="he-control-btn heimgfont"
              v-show="isFull"
              @click.stop="imgToggle"
            >
              &#xe698;
            </div>
            <!--  -->
            <div
              class="he-control-btn heimgfont"
              v-show="!isFull"
              @click.stop="imgToggle"
            >
              &#xe86b;
            </div>
            <!--  -->
            <div class="he-control-btn heimgfont" @click.stop="rotateFunc(-90)">
              &#xe670;
            </div>
            <!--  -->
            <div class="he-control-btn heimgfont" @click.stop="rotateFunc(90)">
              &#xe66f;
            </div>
            <!--  -->
            <!-- <div class="he-control-btn heimgfont" @click.stop="downloadIamge">
              &#xe694;
            </div> -->
          </div>
        </div>
        <!--  -->
        <div
          class="he-control-num"
          v-if="controlBar && multiple"
        >
          {{ imgIndex + 1 }} / {{ imgList.length }}
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  name: 'hevue-img-preview',
  data() {
    return {
      // imgWidth: 0,
      // imgHeight: 0,
      imgScale: 1,
      imgTop: 0,
      imgLeft: 0,
      imgRotate: 0,
      isFull: false,
      maxWH: 'max-width:100%;max-height:100%;',
      clientX: 0,
      clientY: 0,
      imgIndex: 0,
      canRun: true,
      imgurl: '',
      imgState: 1,
      start: [{}, {}],
      mobileScale: 0, // 
      // 
      show: false, // false
      url: '', // 
      nowImgIndex: 0,
      multiple: false,
      imgList: [],
      // 
      controlBar: true,
      closeBtn: true,
      arrowBtn: true,
      keyboard: false,
      clickMaskCLose: false, // false
    }
  },
  mounted() {
    this.initImg()
  },
  watch: {
    url() {
      this.initImg()
    },
    show: {
      handler(newV) {
        if (newV) {
          this.$nextTick(() => {
            let _dom = document.getElementById('hevue-imgpreview-wrap')
            _dom.onmousewheel = this.scrollFunc
            // onmousewheelDOMMouseScroll()
            document.body.addEventListener('DOMMouseScroll', this.scrollFunc)
            // 
            document.ondragstart = function() {
              return false
            }
            // 
            if (this.multiple) {
              if (Array.isArray(this.imgList) && this.imgList.length > 0) {
                this.imgIndex = Number(this.nowImgIndex) || 0
                // this.url = this.imgList[this.imgIndex]
                this.changeUrl(this.imgList[this.imgIndex], this.imgIndex)
              } else {
                // console.error("imgList ");
              }
            } else {
              this.changeUrl(this.url)
            }
            // 
            if (this.keyboard) {
              document.addEventListener('keydown', this.keyHandleDebounce)
            }
          })
        }
      },
      immediate: true,
    },
  },
  methods: {
    close() {
      // this.initImg();
      // this.maxWH = "max-width:100%;max-height:100%;";
      // this.isFull = false;
      // 
      document.body.removeEventListener('DOMMouseScroll', this.scrollFunc)
      //Safari
      document.ondragstart = null
      // 
      if (this.keyboard) {
        document.removeEventListener('keydown', this.keyHandleDebounce)
      }
      this.show = false
    },
    initImg() {
      this.mobileScale = 1
      this.imgScale = 1
      this.imgRotate = 0
      this.imgTop = 0
      this.imgLeft = 0
    },
    /**
     * 
     * true 
     * false 
     */
    toogleImg(bool) {
      if (bool) {
        this.imgIndex++
        if (this.imgIndex > this.imgList.length - 1) {
          this.imgIndex = 0
        }
      } else {
        this.imgIndex--
        if (this.imgIndex < 0) {
          this.imgIndex = this.imgList.length - 1
        }
      }
      // this.url = this.imgList[this.imgIndex]
      this.changeUrl(this.imgList[this.imgIndex], this.imgIndex)
    },
    // 
    /**
     * @description:
     * @param {String} url url
     * @param {Number} index 
     * @return {*}
     */
    changeUrl(url, index) {
      this.imgState = 1
      let img = new Image()
      img.src = url
      img.onload = () => {
        // 
        if (index != undefined && index == this.imgIndex) {
          this.imgState = 2
          this.imgurl = url
        } else if (index == undefined) {
          this.imgState = 2
          this.imgurl = url
        }
      }
      img.onerror = () => {
        if (index != undefined && index == this.imgIndex) {
          this.imgState = 3
        } else if (index == undefined) {
          this.imgState = 3
        }
      }
    },
    // 
    rotateFunc(deg) {
      this.imgRotate += deg
    },
    // 
    scaleFunc(num, bool) {
      if (this.imgScale <= 0.2 && num < 0) return
      if (bool) {
        this.imgScale = num
      } else {
        this.imgScale += num
      }
    },
    // 
    imgToggle() {
      this.initImg()
      if (this.isFull) {
        this.maxWH = 'max-width:100%;max-height:100%;'
      } else {
        this.maxWH = ''
      }
      this.isFull = !this.isFull
    },
    // 
    scrollFunc(e) {
      e = e || window.event
      // e.returnValue = false // ie
      // wheelDeltadetaildetailwheelDelta
      e.delta = e.wheelDelta || -e.detail

      e.preventDefault()
      if (e.delta > 0) {
        //
        this.scaleFunc(0.05)
      }
      if (e.delta < 0) {
        //
        this.scaleFunc(-0.05)
      }
    },
    // 
    addMove(e) {
      e = e || window.event
      this.clientX = e.clientX
      this.clientY = e.clientY
      this.$refs.heImg.onmousemove = this.moveFunc
    },
    // 
    addMoveMobile(e) {
      e.preventDefault()
      e = e || window.event
      if (e.touches.length > 1) {
        this.start = e.touches
      } else {
        this.clientX = e.touches[0].pageX
        this.clientY = e.touches[0].pageY
      }
      // 
      this.$refs.heImg.ontouchmove = this.moveFuncMobile
    },
    // 
    moveFunc(e) {
      e = e || window.event
      e.preventDefault()
      let movementX = e.clientX - this.clientX
      let movementY = e.clientY - this.clientY
      // event.clientY;
      this.imgLeft += movementX * 2
      this.imgTop += movementY * 2
      this.clientX = e.clientX
      this.clientY = e.clientY
    },
    // 
    moveFuncMobile(e) {
      e = e || window.event
      // console.log(e);
      if (e.touches.length > 1) {
        var now = e.touches
        var scale =
          this.getDistance(now[0], now[1]) /
          this.getDistance(this.start[0], this.start[1])
        // 
        if (this.mobileScale) {
          if (scale > 1) {
            // 
            this.scaleFunc(scale + this.mobileScale - 1, true)
          } else {
            // 
            this.scaleFunc(scale * this.mobileScale, true)
          }
        } else {
          this.scaleFunc(scale, true)
        }
      } else {
        let touch = e.touches[0]
        e.preventDefault()
        let movementX = touch.pageX - this.clientX
        let movementY = touch.pageY - this.clientY
        // event.clientY;
        this.imgLeft += movementX * 2
        this.imgTop += movementY * 2
        this.clientX = touch.pageX
        this.clientY = touch.pageY
      }
    },
    // 
    removeMove(type) {
      if (type === 'pc') {
        this.$refs.heImg.onmousemove = null
      } else {
        this.mobileScale = this.imgScale
        this.$refs.heImg.ontouchmove = null
      }
    },
    keyHandleDebounce(e) {
      if (this.canRun) {
        // this.canRuntrue
        this.keyHandle(e)
        this.canRun = false // 
        setTimeout(() => {
          this.canRun = true // this.canRuntrue
        }, 300)
      }
    },
    // 
    keyHandle(e) {
      e = window.event || e
      var key = e.keyCode || e.which || e.charCode
      switch (key) {
        case 27: // esc
          this.close()
          break
        case 65: // a-
          if (this.multiple) {
            this.toogleImg(false)
          }
          break
        case 68: // d-
          if (this.multiple) {
            this.toogleImg(true)
          }
          break
        case 87: // w-
          this.scaleFunc(0.15)
          break
        case 83: // s-
          this.scaleFunc(-0.15)
          break
        case 81: // q-
          this.rotateFunc(-90)
          break
        case 69: // e-
          this.rotateFunc(90)
          break
        case 82: // r-
          this.initImg()
          break

        default:
          break
      }
    },
    // 
    clickMask() {
      // console.log("hello");
      if (this.clickMaskCLose) {
        this.close()
      }
    },
    // -
    getDistance(p1, p2) {
      var x = p2.pageX - p1.pageX,
        y = p2.pageY - p1.pageY
      return Math.sqrt(x * x + y * y)
    },
    /**
     * @description:
     * @param {String} imgsrc
     * @param {*} name
     * @return {*}
     */
    downloadIamge() {
      //
      let image = new Image()
      //  Canvas 
      image.setAttribute('crossOrigin', 'anonymous')
      image.onload = function() {
        let canvas = document.createElement('canvas')
        canvas.width = image.width
        canvas.height = image.height
        let context = canvas.getContext('2d')
        context.drawImage(image, 0, 0, image.width, image.height)
        let url = canvas.toDataURL('image/png') //base64
        let a = document.createElement('a') // a
        let event = new MouseEvent('click') // 
        a.download = 'photo' + +new Date() // 
        a.href = url // URLa.href
        a.dispatchEvent(event) // a
      }
      image.onerror = function(err) {
        console.log('')
        console.log(err)
      }
      if (this.multiple) {
        image.src = this.imgList[this.imgIndex]
      } else {
        image.src = this.url
      }
    },
  },
}
</script>

<style scoped>
@import './iconfont/iconfont.css';
@import './css/default.css';
</style>
